import functions from './functions.js';
import instructions from './instructions.js';
/**
 * @description Function specs for the assistant
 * @private
 * @function functionSpecs
 * @returns {object} - object containing specs, tools, functionList
 * 
 */

function functionSpecs(provider, code, retrieval) {
  let specs = [
    _listSASObjectsFunctionSpec,
    _listSASDataLibFunctionSpec,
    _listSASTablesFunctionSpec,
    _listColumnsFunctionSpec,
    _describeTableSpec,
    _catalogFunctionSpec,
    _getDataFunctionSpec,
    _runSASFunctionSpec,
    _keywordsFunctionSpec,
   // _contextDataFunctionSpec,
  ];


  // Create tools array  for use with Assistant API
  let tools = [];
  if (code) {
    tools.push({ type: 'code_interpreter' });
  }
  if (retrieval) {
    tools.push({ type: 'retrieval' });
  }

  specs.forEach((f) => {
    let r = {
      type: "function",
      function: Object.assign({}, f),
    };
    tools.push(r);
  });

  let functionList = functions();
  debugger;
  let instruction = instructions();
  debugger;
  return { specs: specs, tools: tools, functionList: functionList, instructions: instruction };
}
const _catalogFunctionSpec = {
  name: '_catalogSearch',
  description: `Search for the specified metadata in SAS Viya.
      the search is specified as a comma delimited string like libname:casuser,Columns:Make,name:abc
      Convert string to a query string using these patterns:
      libname xxx to libname:xxx
      Column xxx to Column.name:xxx
      format results from a seach as html elements.
      Use either unordered lists, tables or nested tables based. Below are some examples
      Format the response as a html table if the content of the response is one of the following formats:

  - a comma-delimited format 
  - or of this format [{a:1,b:2},{a:1,b:3},...]

  The html table should have a light blue background for the column headers.Below is a sample html table format.
  Use a border width of 1px and solid style for the table.
  '<table>
     <tr>
       <th>a</th> 
      <th>b</th>
     </tr>
    <tr>
    <td>1</td>
    <td>2</td>
    </tr>
    <tr>
   <td>2</td>
   <td>3</td>
   </tr>
   </table>' 
 
  if the response from a tool is of the form  like ['a','b','c', ...] or [1,11,8, ...] then return the data as a html unordered list to the user
  like this:
  '<ul>
    <li>a</li>
    <li>b</li>
    <li>3</li>
  </ul>'

  if the response from a tool is of the form {a:1,b:2} then return the data as a html table with a single column.
  '<table>
  <tr>
  <th>Name</th>
  <th>Value</th>
  </tr>
    <tr>
      <td>a</td>
      <td>1</td>
    </tr>
    <tr>
      <td>b</td>
      <td>1</td>
    </tr>

  </table>'

 if the response from a tool is of the form 
 {a: {a1:10, bx:20, c: {cx:3, az: 4}} } then format the message as nested html table. Here is an example:

  '<table>
  <tr>
  <th>Name</th>
  <th>Value</th>
  </tr>
  <tr>
  <td>a</td>
  <td>
  <table>
  <tr>
  <th>Name</th>
  <th>Value</th>
  </tr>
  <tr>
  <td>a1</td>
  <td>10</td>
  </tr>
  <tr>
  <td>bx</td>
  <td>20</td>
  </tr>
  <tr>
  <td>c</td>
  <td>
  <table>
  <tr>
  <th>Name</th>
  <th>Value</th>
  </tr>
  <tr>
  <td>cx</td>
  <td>3</td>
  </tr>
  <tr>
  <td>az</td>
  <td>4</td>
  </tr>
  </table>
  </td>
  </tr>
  </table>
  </td>
  </tr>

  </table>'
  
      `,
  parameters: {
    properties: {
      metadata: {
        type: 'string',
        description: 'The metadata to return',
      },
    },
    type: 'object',
    required: ['metadata'],
  }
};

const _getDataFunctionSpec = {
  name: '_getData',
  description: `Fetch data from a  table like casuser.cars.
                To limit the number of rows, specify the limit parameter.
                If format is true, then the data will be formatted.
                Use standard where clause to filter the data.
                To return data in csv format, specify csv = true. Default is false.`,
  parameters: {
    properties: {
      table: {
        type: 'string',
        description:
          'The table to setup. The form of the table is casuser.cars',
      },
      limit: {
        type: 'integer',
        description: 'Fetch only the specified number of rows'
      },
      'format': {
        type: 'boolean',
        description: 'Format the string - true or false'
      },  
      where: {
        type: 'string',
        description: 'A where clause like Make eq "Audi"'
      },
      csv: {
        type: 'boolean',
        description: 'Return data in csv format - true or false'
      }
    },
    type: 'object',
    required: ['table'],
  },
};
